/* General Styles */
body {
    background-color: #001f3f !important; /* Matte navy blue background */
    color: #ffffff !important; /* White text color */
    font-family: Arial, sans-serif; /* Ensure a fallback font family */
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
  }
  
  a {
    color: #d35400; /* Dark matte orange links */
  }
  
  a:hover {
    color: #b7410e; /* Even darker orange links */
  }
  
  h1, h2, p {
    color: #ffcc00 !important; /* Bright yellow for better visibility */
  }
  
  .container-fluid, .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 0 20px;
    background-color: #001f3f !important; /* Matte navy blue background for container */
  }
  
  .row {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; /* Ensure items wrap on smaller screens */
  }
  
  .col-md-4, .col-md-5 {
    margin: 10px;
    flex: 0 0 100%; /* Default to full width for smaller screens */
  }
  
  .navbar {
    background-color: #d35400; /* Dark matte orange */
  }
  
  .nav-link, .navbar-brand {
    color: #ffffff !important; /* White text color */
  }
  
  .nav-link:hover {
    background-color: #b7410e !important; /* Even darker orange */
    color: #ffffff !important; /* White text */
  }
  
  /* Card Styles */
  .card {
    background-color: #ffffff !important; /* White background for cards */
    border: 1px solid #dee2e6 !important;
    border-radius: 0.25rem !important;
    color: #001f3f !important; /* Navy blue text color */
    margin-bottom: 20px !important;
    width: 100% !important; /* Ensuring the cards take up full width */
    max-width: 400px !important; /* Maximum width for the cards */
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
  }
  
  .card-body {
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
    height: 100% !important; /* Ensures cards are the same height */
  }
  
  .card-header {
    background-color: #d35400 !important; /* Dark matte orange */
    color: #ffffff !important; /* White text */
    width: 100% !important;
    text-align: center !important;
    padding: 10px 0 !important;
    font-size: 1.5rem !important;
  }
  
  /* Specific card text styles */
  .card-text {
    color: #333 !important; /* Dark grey text */
    font-size: 0.875rem;
    margin-bottom: 1rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3; /* Number of lines to display */
    max-height: 4.5rem; /* Control the height */
  }
  
  .card-text.expanded {
    -webkit-line-clamp: unset; /* Reset line clamp for expanded text */
    max-height: none; /* Remove max height for expanded text */
  }
  
  /* Form Styles */
  .form-control {
    background-color: #f8f9fa !important; /* Light grey background */
    color: #001f3f !important; /* Navy blue text */
    border: 1px solid #ccc !important;
  }
  
  .form-control::placeholder {
    color: #666 !important; /* Grey placeholder text */
  }
  
  .btn-custom {
    background-color: #d35400 !important; /* Dark matte orange */
    color: #ffffff !important; /* White */
  }
  
  .btn-custom:hover {
    background-color: #b7410e !important; /* Even darker orange */
    color: #ffffff !important; /* White */
  }
  
  .btn-read-more {
    background-color: transparent !important;
    border: none !important;
    color: #d35400 !important; /* Dark matte orange */
    cursor: pointer !important;
  }
  
  .btn-read-more:hover {
    color: #b7410e !important; /* Even darker orange */
  }
  
  /* Media Queries for Responsive Design */
  @media (min-width: 768px) {
    .col-md-4, .col-md-5 {
      flex: 0 0 48%; /* Adjust to half width for larger screens */
    }
  }
  